.container {
    display: flex;
}

@media screen and (min-width: 320px) {
.container {
    max-width: 320px;
    margin: 0 auto;
    padding: 0 20px;
}
}

@media screen and (min-width: 768px) {
.container {
    max-width: 768px;
    padding: 0 34px;
}
}

@media screen and (min-width: 1280px) {
.container {
    max-width: 1280px;
    padding: 0 115px;
}
}

.container--about {
    flex-wrap: wrap;
    justify-content: center;
}